export class UserInfo {
  constructor(profileName, profileCareer, avatar) { 
    this._name = profileName;
    this._about = profileCareer;
    this._avatar = avatar;
  }

  getUserInfo() {
    return {
      name: this._name.textContent,
      about: this._about.textContent,
    };
  }
  setUserInfo(result) {
    this._avatar.src = result.avatar;
    this._name.textContent = result.name;
    this._about.textContent = result.about;
    this._id = result._id;
  }
  getUserAvatar(item) {
    this._avatar.src = item.link;
  }
  // getUserId(result) {
  //   return this._id = result._id;
  // }
}
